export function fetchInitial(payload) {
  return dispatch => {
    request(`${process.env.REACT_APP_API_URl}/rest/order?${stringify(payload)}`).then(response => {
      const { data } = response;
      const { departTimeStr, arriveTimeStr, arriveDate, durationStr, price } = data;
      dispatch(setDeparttimestr(departTimeStr));
      dispatch(setArrivetimestr(arriveTimeStr));
      dispatch(setArrivedate(arriveDate));
      dispatch(setDurationstr(durationStr));
      dispatch(setPrice(price));
    });
  };
}

let passengerIdSeed = 0;

export function createAdult() {
  return (dispatch, getState) => {
    const { passengers } = getState();
    for (const passenger of passengers) {
      for (const key of Object.keys(passenger)) {
        if (!passenger[key]) {
          return;
        }
      }
    }
    dispatch(
      setPassengers([
        ...passengers,
        {
          id: ++passengerIdSeed,
          name: "",
          ticketType: "adult",
          licenceNo: "",
          seat: "Z",
        },
      ])
    );
  };
}

export function createChild() {
  return (dispatch, getState) => {
    const { passengers } = getState();
    let foundAdult = false;
    for (const passenger of passengers) {
      for (const key of Object.keys(passenger)) {
        if (!passenger[key]) {
          return;
        }
      }
      if (passenger.ticketType === "adult") {
        foundAdult = passenger.id;
      }
    }
    if (!foundAdult) {
      alert("添加儿童必须先添加成人");
      return;
    }
    dispatch(
      setPassengers([
        ...passengers,
        {
          id: ++passengerIdSeed,
          name: "",
          ticketType: "child",
          gender: "none",
          followAdult: foundAdult,
          seat: "Z",
        },
      ])
    );
  };
}

export function removePassenger(id) {
  return (dispatch, getState) => {
    const { passengers } = getState();
    const newPassengers = passengers.filter(
      passenger => passenger.id !== id && passenger.followAdult !== id
    );

    dispatch(setPassengers(newPassengers));
  };
}

export function updatePassenger(payload, keysToBeRemoved = []) {
  return (dispatch, getState) => {
    const { passengers } = getState();
    for (let i = 0, l = passengers.length; i < l; ++i) {
      if (passengers[i].id === payload.id) {
        passengers[i] = Object.assign({}, { ...passengers[i] }, { ...payload });

        for (let key of keysToBeRemoved) {
          delete passengers[i][key];
        }
        break;
      }
    }
    dispatch(setPassengers([...passengers]));
  };
}

export function showMenu(menu) {
  return dispatch => {
    dispatch(setMenu(menu));
    dispatch(setIsmenuvisible(true));
  };
}

export function hideMenu() {
  return dispatch => {
    dispatch(setIsmenuvisible(false));
  };
}

export function showGenderMenu(id) {
  return (dispatch, getState) => {
    const { passengers } = getState();
    const passenger = passengers.find(passenger => passenger.id === id);
    if (!passenger) {
      return;
    }

    dispatch(
      showMenu({
        onPress(gender) {
          dispatch(updatePassenger({ id, gender }));
          dispatch(hideMenu());
        },
        options: [
          {
            title: "男",
            value: "male",
            active: "male" === passenger.gender,
          },
          {
            title: "女",
            value: "female",
            active: "female" === passenger.gender,
          },
        ],
      })
    );
  };
}

export function showFollowAdultMenu(id) {
  return (dispatch, getState) => {
    const { passengers } = getState();
    const passenger = passengers.find(passenger => passenger.id === id);
    if (!passenger) {
      return;
    }

    dispatch(
      showMenu({
        onPress(followAdult) {
          dispatch(updatePassenger({ id, followAdult }));
          dispatch(hideMenu());
        },
        options: passengers
          .filter(passenger => passenger.ticketType === "adult")
          .map(adult => ({
            title: adult.name,
            value: adult.id,
            active: adult.id === passenger.followAdult,
          })),
      })
    );
  };
}

export function showTicketTypeMenu(id) {
  return (dispatch, getState) => {
    const { passengers } = getState();
    const passenger = passengers.find(passenger => passenger.id === id);
    if (!passenger) {
      return;
    }

    dispatch(
      showMenu({
        onPress(ticketType) {
          if ("adult" === ticketType) {
            dispatch(
              updatePassenger({ id, ticketType, licenceNo: "" }, [
                "gender",
                "followAdult",
                "birthday",
              ])
            );
          } else {
            const adult = passengers.find(
              passenger => passenger.id !== id && passenger.ticketType === "adult"
            );
            if (adult) {
              dispatch(
                updatePassenger(
                  { id, ticketType, gender: "", followAdult: adult.id, birthday: "" },
                  ["licenceNo"]
                )
              );
            } else {
              alert("请先添加成人乘客");
            }
          }
          dispatch(hideMenu());
        },
        options: [
          {
            title: "成人票",
            value: "adult",
            active: "adult" === passenger.ticketType,
          },
          {
            title: "儿童票",
            value: "child",
            active: "child" === passenger.ticketType,
          },
        ],
      })
    );
  };
}
