import React, { useCallback, useEffect } from "react";
import { connect } from "react-redux";
import URI from "urijs";
import dayjs from "dayjs";
import {
  setTrainnumber,
  setDepartstation,
  setArrivestation,
  setDepartdate,
  setSeattype,
  setSearchparsed,
  fetchInitial,
  createAdult,
  createChild,
  removePassenger,
  updatePassenger,
  showGenderMenu,
  hideMenu,
  showFollowAdultMenu,
  showTicketTypeMenu,
} from "./action";
import Header from "../components/Header";
import Account from "./Account";
import Choose from "./Choose";
import Passengers from "./Passengers";
import Ticket from "./Ticket";
import Detail from "../components/Detail";
import Menu from "./Menu";
import "./App.css";

function App(props) {
  const {
    trainNumber,
    departStation,
    arriveStation,
    seatType,
    departDate,
    arriveDate,
    departTimeStr,
    arriveTimeStr,
    durationStr,
    price,
    passengers,
    menu,
    isMenuVisible,
    searchParsed,
    setTrainnumber,
    setDepartstation,
    setArrivestation,
    setDepartdate,
    setSeattype,
    setSearchparsed,
    fetchInitial,
    createAdult,
    createChild,
    removePassenger,
    updatePassenger,
    showGenderMenu,
    hideMenu,
    showFollowAdultMenu,
    showTicketTypeMenu,
  } = props;

  const onBack = useCallback(() => {
    window.history.back();
  }, []);

  useEffect(() => {
    const queries = URI.parseQuery(window.location.search);
    const { trainNumber, departStation, arriveStation, departDate, type } = queries;
    setTrainnumber(trainNumber);
    setDepartstation(departStation);
    setArrivestation(arriveStation);
    setDepartdate(dayjs(departDate).valueOf());
    setSeattype(type);
    setSearchparsed(true);
  }, [
    setArrivestation,
    setDepartdate,
    setDepartstation,
    setSearchparsed,
    setSeattype,
    setTrainnumber,
  ]);

  useEffect(() => {
    if (!searchParsed) {
      return;
    }
    fetchInitial({ departStation, arriveStation, type: seatType, date: dayjs(departDate).format });
  }, [arriveStation, departDate, departStation, fetchInitial, searchParsed, seatType]);

  if (!searchParsed) {
    return null;
  }

  return (
    <div className="app">
      <div className="header-wrapper">
        <Header title="订单填写" onBack={onBack} />
      </div>
      <div className="detail-wrapper">
        <Detail
          departDate={departDate}
          arriveDate={arriveDate}
          departTimeStr={departTimeStr}
          arriveTimeStr={arriveTimeStr}
          departStation={departStation}
          arriveStation={arriveStation}
          trainNumber={trainNumber}
          durationStr={durationStr}
        >
          <span style={{ display: "block" }} className="train-icon"></span>
        </Detail>
      </div>
      <Ticket price={price} type={seatType} />
      <Passengers
        passengers={passengers}
        createAdult={createAdult}
        createChild={createChild}
        removePassenger={removePassenger}
        onUpdate={updatePassenger}
        showGenderMenu={showGenderMenu}
        showFollowAdultMenu={showFollowAdultMenu}
        showTicketTypeMenu={showTicketTypeMenu}
      />
      {passengers.length > 0 ? (
        <Choose passengers={passengers} updatePassenger={updatePassenger} />
      ) : null}
      <Account length={passengers.length} price={price} />

      <Menu show={isMenuVisible} {...menu} hideMenu={hideMenu} />
    </div>
  );
}

export default connect(
  state => state,
  {
    setTrainnumber,
    setDepartstation,
    setArrivestation,
    setDepartdate,
    setSeattype,
    setSearchparsed,
    fetchInitial,
    createAdult,
    createChild,
    removePassenger,
    updatePassenger,
    showGenderMenu,
    hideMenu,
    showFollowAdultMenu,
    showTicketTypeMenu,
  }
)(App);
