import {
  SET_ACTION_TRAINNUMBER,
  SET_ACTION_DEPARTSTATION,
  SET_ACTION_ARRIVESTATION,
  SET_ACTION_SEATTYPE,
  SET_ACTION_DEPARTDATE,
  SET_ACTION_ARRIVEDATE,
  SET_ACTION_DEPARTTIMESTR,
  SET_ACTION_ARRIVETIMESTR,
  SET_ACTION_DURATIONSTR,
  SET_ACTION_PRICE,
  SET_ACTION_PASSENGERS,
  SET_ACTION_MENU,
  SET_ACTION_ISMENUVISIBLE,
  SET_ACTION_SEARCHPARSED,
} from "./action";

export default {
  trainNumber(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_TRAINNUMBER:
        return payload;
      default:
        return state;
    }
  },
  departStation(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_DEPARTSTATION:
        return payload;
      default:
        return state;
    }
  },
  arriveStation(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_ARRIVESTATION:
        return payload;
      default:
        return state;
    }
  },
  seatType(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_SEATTYPE:
        return payload;
      default:
        return state;
    }
  },
  departDate(state = Date.now(), action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_DEPARTDATE:
        return payload;
      default:
        return state;
    }
  },
  arriveDate(state = Date.now(), action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_ARRIVEDATE:
        return payload;
      default:
        return state;
    }
  },
  departTimeStr(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_DEPARTTIMESTR:
        return payload;
      default:
        return state;
    }
  },
  arriveTimeStr(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_ARRIVETIMESTR:
        return payload;
      default:
        return state;
    }
  },
  durationStr(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_DURATIONSTR:
        return payload;
      default:
        return state;
    }
  },
  price(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_PRICE:
        return payload;
      default:
        return state;
    }
  },
  passengers(state = [], action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_PASSENGERS:
        return payload;
      default:
        return state;
    }
  },
  menu(state = "", action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_MENU:
        return payload;
      default:
        return state;
    }
  },
  isMenuVisible(state = false, action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_ISMENUVISIBLE:
        return payload;
      default:
        return state;
    }
  },
  searchParsed(state = false, action) {
    const { type, payload } = action;
    switch (type) {
      case SET_ACTION_SEARCHPARSED:
        return payload;
      default:
        return state;
    }
  },
};
